$sans-serif: sans-serif;
$serif: serif;
// 
$helvetica: 'Helvetica';
$arial: 'Arial';
$verdata: 'Verdana';
//  
$rubik: "Rubik";
$opensans: "Open Sans";
$dmsans: "DMSans";
$poppins: "Poppins";
$roboto: "Roboto";
$quicksand: "Quicksand";
$myriadpro: "MyriadPro";

@mixin font-face($font-family, $url, $weight) {
  @font-face {
    font-family: '#{$font-family}';
    src: url('../font/#{$url}.woff2') format('woff2'),
    url('../font/#{$url}.woff') format('woff');
    font-weight: #{$weight};
    font-display: swap;
    font-style: normal;
  }
}

// 
// @include font-face(Quicksand, Quicksand-Light, 300);

@mixin safe-font($font-family) {
  font-family: '#{$font-family}',
  $helvetica,
  $arial $sans-serif;
}

// 
//   @include safe-font(Rubik); //(Rubik)   


@mixin font($font-family, $font-weight, $font-size) {
  font-family: "#{$font-family}",
  sans-serif;
  font-weight: $font-weight;
  font-size: #{$font-size}px;
}

// .main {
//   @include font(Rubik, 700, 58)
// }

@mixin font-100($font-size) {
  font-weight: 100;
  font-size: #{$font-size}px;
}

@mixin font-200($font-size) {
  font-weight: 200;
  font-size: #{$font-size}px;
}

@mixin font-300($font-size) {
  font-weight: 300;
  font-size: #{$font-size}px;
}

@mixin font-400($font-size) {
  font-weight: 400;
  font-size: #{$font-size}px;
}

@mixin font-500($font-size) {
  font-weight: 500;
  font-size: #{$font-size}px;
}

@mixin font-600($font-size) {
  font-weight: 600;
  font-size: #{$font-size}px;
}

@mixin font-700($font-size) {
  font-weight: 700;
  font-size: #{$font-size}px;
}

@mixin font-800($font-size) {
  font-weight: 800;
  font-size: #{$font-size}px;
}

@mixin font-900($font-size) {
  font-weight: 900;
  font-size: #{$font-size}px;
}

@mixin font-950($font-size) {
  font-weight: 950;
  font-size: #{$font-size}px;
}

